// @flow

import { toPublic } from '../core/privateKey'
import {
  type DerivedPrivateKey,
  type DerivedPublicKey,
  type DerivedKeyPair,
  type DerivedPoint
} from '../../types/hd.js'
import { hmacSplit } from '../utils/hash.js'
import * as Secp256k1 from '../utils/secp256k1'

export const HARDENED = 0x80000000
export const MAX_INDEX = 0xffffffff
export const TWEAK_OUT_OF_RANGE_ERROR = 'tweak out of range'

export const getTweak = async (
  key: string,
  entropy: string,
  index?: number = 0
): Promise<DerivedPoint> => {
  if (index > MAX_INDEX) throw new Error('Index out of range.')
  key += index.toString(16).padStart(8, '0')
  const { left, right } = hmacSplit(key, entropy)
  return { tweakPoint: left, chainCode: right }
}

export const derivePublic = async (
  publicKey: string,
  index: number,
  entropy: string
): Promise<DerivedPublicKey> => {
  if (index >= HARDENED) {
    throw new Error('Cannot derive a hardened index with a public key.')
  }

  try {
    const { tweakPoint, chainCode } = await getTweak(
      publicKey,
      entropy,
      index
    )
    const childKey = await Secp256k1.publicKeyTweakAdd(
      publicKey,
      tweakPoint,
      true
    )
    return { publicKey: childKey, index, chainCode }
  } catch (e) {
    if (!e.message.includes(TWEAK_OUT_OF_RANGE_ERROR)) throw e
    return derivePublic(publicKey, index + 1, entropy)
  }
}

export const derivePrivate = async (
  privateKey: string,
  index: number,
  entropy: string,
  hardened: boolean = false,
  publicKey?: string
): Promise<DerivedPrivateKey> => {
  if (hardened && index < HARDENED) index += HARDENED
  let key = `00${privateKey}`
  if (index < HARDENED) {
    key = publicKey || await toPublic(privateKey, true)
  }

  try {
    const { tweakPoint, chainCode } = await getTweak(
      key,
      entropy,
      index
    )
    const childKey = await Secp256k1.privateKeyTweakAdd(privateKey, tweakPoint)
    return { privateKey: childKey, index, chainCode }
  } catch (e) {
    if (!e.message.includes(TWEAK_OUT_OF_RANGE_ERROR)) throw e
    if (index > MAX_INDEX) index -= HARDENED
    return derivePrivate(privateKey, index + 1, entropy, hardened, publicKey)
  }
}

export const deriveKeyPair = async (
  parentKeys: DerivedKeyPair,
  indexStr?: string = '0'
): Promise<DerivedKeyPair> => {
  const hardened = indexStr[indexStr.length - 1] === "'"
  const index = hardened ? parseInt(indexStr.slice(0, -1)) : parseInt(indexStr)
  const { chainCode, privateKey, publicKey } = parentKeys

  if (privateKey) {
    const childKey = await derivePrivate(
      privateKey,
      index,
      chainCode,
      hardened,
      publicKey
    )
    const childPublicKey = await toPublic(childKey.privateKey, true)
    return { ...childKey, publicKey: childPublicKey }
  } else if (hardened) {
    throw new Error('Cannot derive a hardened index without a private key.')
  }
  // $FlowFixMe
  return derivePublic(publicKey, index, chainCode)
}
