// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import { fromWIF, toPublic } from '../../src/core/privateKey.js'

import keys from './fixtures.json'

const network = 'main'

describe('Testing HD Key', function () {
  keys.forEach(test => {
    it(`Creating Private Key from WIF ${test[0]}`, async function () {
      const { privateKey, compress } = fromWIF(test[0], network)
      console.log('privateKey', privateKey)
      console.log('compress', compress)
      const publicKey = await toPublic(privateKey, compress)
      assert.equal(publicKey, test[2])
    })

    // it(`Creating Key Pair from WIF ${test[0]}`, async function () {
    //   const { privateKey, compress } = fromWIF(test[0], network)
    //   const publicKey = await toPublic(privateKey, compress)
    //   assert.equal(publicKey, test[1])
    // })
  })
})
