// @flow

import {
  type DerivedKeyPair,
  type ExtendedKeyPair
} from '../../types/hd.js'
import { toPublic } from '../core/privateKey.js'
import {
  getDecoder,
  getHDSetting
} from '../core/networkInfo.js'
import { hash160, hmacSplit } from '../utils/hash.js'
import { deriveKeyPair } from './derive.js'

const MAX_DEPTH = 0xff
const SEED = '426974636f696e2073656564'

export const fromSeed = async (
  seed: string,
  network: string = 'main',
  purpose: number = 32
): Promise<ExtendedKeyPair> => {
  const { left, right } = hmacSplit(seed, SEED)
  const publicKey = await toPublic(left, true)
  const masterKeyPair = {
    privateKey: left,
    publicKey,
    chainCode: right,
    index: 0
  }
  const version = getHDSetting(purpose, network).xpriv.prefix
  return {
    ...masterKeyPair,
    parentFingerPrint: 0,
    version: parseInt(version),
    depth: 0
  }
}

export const fromIndex = async (
  parentKeys: ExtendedKeyPair,
  index: string
): Promise<ExtendedKeyPair> => {
  if (parentKeys.depth >= MAX_DEPTH) throw new Error('Depth too high.')

  const derivedKeyPair: DerivedKeyPair = await deriveKeyPair(parentKeys, index)
  if (!parentKeys.publicKey) {
    if (!parentKeys.privateKey) {
      throw new Error('Cannot create parentFingerPrint without keys')
    }
    parentKeys.publicKey = await toPublic(parentKeys.privateKey, true)
  }
  const parentFingerPrint = await hash160(parentKeys.publicKey)

  return {
    ...derivedKeyPair,
    parentFingerPrint: parseInt(parentFingerPrint.slice(0, 8), 16),
    version: parentKeys.version,
    depth: parentKeys.depth + 1
  }
}

export const fromHex = (keyHex: string, network?: string): ExtendedKeyPair => {
  // Check the entire hex length
  if (keyHex.length !== 156) throw new Error('Wrong pair length')

  // Check that the key prefix matches the network's prefix if given or any network if not
  const version = parseInt(keyHex.slice(0, 8), 16)
  const { xpriv, xpub } = getHDSetting(version, network)

  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)

  const key = {}

  if (headerByte === 0 && version === xpriv.prefix) {
    key.privateKey = keyHex.slice(92, 156)
  } else if (
    (headerByte === 2 || headerByte === 3) &&
    version === xpub.prefix
  ) {
    key.publicKey = keyHex.slice(90, 156)
  } else {
    throw new Error('Bad key prefix')
  }

  return {
    ...key,
    version,
    depth: parseInt(keyHex.slice(9, 10), 16),
    parentFingerPrint: parseInt(keyHex.slice(10, 18), 16),
    index: parseInt(keyHex.slice(18, 26), 16),
    chainCode: keyHex.slice(26, 90)
  }
}

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedKeyPair => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}

export const toHex = (
  hdKey: ExtendedKeyPair,
  network: string = 'main',
  forcePublic: boolean = false
): string => {
  const { xpub, xpriv } = getHDSetting(hdKey.version, network)
  const { privateKey, publicKey } = hdKey
  let key = publicKey
  let version = xpub.prefix

  if (privateKey && !forcePublic) {
    key = `00${privateKey}`
    version = xpriv.prefix
  }

  return (
    parseInt(version).toString(16).padStart(8, '0') +
    hdKey.depth.toString(16).padStart(2, '0') +
    hdKey.parentFingerPrint.toString(16).padStart(8, '0') +
    hdKey.index.toString(16).padStart(8, '0') +
    hdKey.chainCode +
    key
  )
}

export const toString = (
  hdKey: ExtendedKeyPair,
  network: string = 'main',
  forcePublic: boolean = false
): string => {
  const keyHex = toHex(hdKey, network, forcePublic)
  return getDecoder(network, hdKey.version).encode(keyHex)
}
