// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import * as Derive from '../../src/hd/derive.js'
import fixtures from './fixtures/derive.json'

const deriveFixtures = fixtures

describe('Testing Key derivation', function () {
  deriveFixtures.private.forEach(test => {
    it(`Deriving private key without public key ${test[0]} for index ${
      test[2]
    }`, async function () {
      const privateKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const expectedChildKey = {
        privateKey: test[3],
        chainCode: test[4],
        index: test[5]
      }
      const hardened = test[6]
      const childKey = await Derive.derivePrivate(
        privateKey,
        index,
        chainCode,
        hardened
      )
      assert.deepEqual(childKey, expectedChildKey)
    })
  })

  deriveFixtures.privateWithPublic.forEach(test => {
    it(`Deriving private key with public key ${test[0]} for index ${
      test[2]
    }`, async function () {
      const privateKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const expectedChildKey = {
        privateKey: test[3],
        chainCode: test[4],
        index: test[5]
      }
      const publicKey = test[6]
      const childKey = await Derive.derivePrivate(
        privateKey,
        index,
        chainCode,
        false,
        publicKey
      )
      assert.deepEqual(childKey, expectedChildKey)
    })
  })

  deriveFixtures.public.forEach(test => {
    it(`Deriving public key ${test[0]} for index ${test[2]}`, async function () {
      const publicKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const expectedChildKey = {
        publicKey: test[3],
        chainCode: test[4],
        index: test[5]
      }
      const childKey = await Derive.derivePublic(
        publicKey,
        index,
        chainCode
      )
      assert.deepEqual(childKey, expectedChildKey)
    })
  })
})
